html {
  font-size: 6.25%; /*  1rem = 1px */
  box-sizing: border-box; /*box mnodel*/
}

*,
*:before,
*:after {
  box-sizing: inherit; /*box mnodel*/
  margin: 0;
  padding: 0;
}

body {
  font-size: 16rem;
  font-family: Arial, Helvetica, sans-serif;
  background-color: #1d1d1d;
  display: flex;
  justify-content: center;
  color: #ffffff;
}

h1,
h2,
h3 {
  color: #c0f205;
  font-family: Arial, Helvetica, sans-serif;
}

h1 {
  font-size: 40rem;
}

h2 {
  font-size: 36rem;
}

h3 {
  font-size: 30rem;
}

p {
  font-size: 20rem;
}

img {
  width: 100%;
  height: auto;
  display: block;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
  color: #ffffff;
}

.contenedor {
  width: 90%;
}

.index {
  display: grid;
  grid-template-areas: " aside header header header" " main main main main" " main main main main" " footer  footer footer footer";
}
.index .header {
  grid-area: header;
}
.index .aside {
  grid-area: aside;
}
.index .main {
  grid-area: main;
}
.index .footer {
  grid-area: footer;
}
@media (min-width: 992px) {
  .index {
    grid-template-areas: " aside header header header" " aside main main main" " aside main main main" " blanco  footer footer footer";
  }
}

.header {
  display: flex;
  align-items: center;
}

.nav {
  position: absolute;
  top: 0;
  left: -400rem;
  width: 100%;
  height: 100vh;
  background-color: #393939;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 800ms;
}
@media (min-width: 480px) {
  .nav {
    position: inherit;
    height: inherit;
    background-color: inherit;
    justify-content: flex-end;
    padding: 20rem;
    transition: none;
  }
}

.nav-active {
  transition: all 800ms;
  left: 0;
}
@media (min-width: 480px) {
  .nav-active {
    transition: none;
  }
}

.nav-ul {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 300rem;
  font-size: 30rem;
  text-align: center;
}
@media (min-width: 480px) {
  .nav-ul {
    text-align: inherit;
    font-size: inherit;
    height: inherit;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 400rem;
  }
}

.nav-li a:hover {
  color: #c0f205;
}

.activo {
  color: #c0f205;
}

.elipse {
  position: absolute;
  top: 25rem;
  right: 53rem;
  width: 50rem;
  margin-left: 100rem;
  border-radius: 50%;
}
.elipse:hover {
  cursor: pointer;
}
@media (min-width: 480px) {
  .elipse {
    position: inherit;
    width: 25rem;
  }
}

.btn {
  background-color: #c0f205;
}
@media (min-width: 480px) {
  .btn {
    background-color: inherit;
  }
}

.aside {
  padding: 20rem;
}
.aside img {
  width: 50rem;
}
@media (min-width: 768px) {
  .aside {
    border-right: 1px solid #ffffff;
  }
}

.main section {
  margin-bottom: 50rem;
}
@media (min-width: 480px) {
  .main {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 50rem;
  }
}

.footer {
  background-color: #393939;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  height: 600rem;
  padding: 10rem;
  border-radius: 10rem;
}
@media (min-width: 480px) {
  .footer {
    height: 200rem;
    flex-direction: row;
    align-items: flex-start;
    justify-content: space-evenly;
    padding-top: 30rem;
    border-radius: 0;
    -webkit-box-shadow: inset -5px -5px 8px 0px rgb(237, 230, 237);
    -moz-box-shadow: inset -5px -5px 8px 0px rgb(237, 230, 237);
    box-shadow: inset -5px -5px 8px 0px rgb(237, 230, 237);
  }
}

.footer-sec {
  text-align: center;
}
@media (min-width: 480px) {
  .footer-sec {
    width: 250rem;
  }
  .footer-sec:nth-child(3) {
    width: 300rem;
  }
}
.footer-sec h2 {
  font-size: 16rem;
  text-transform: uppercase;
}
.footer-sec p {
  line-height: 1.7;
  font-size: 13rem;
}

.services {
  display: grid;
  grid-template-areas: " header header header header" " main main main main" " main main main main" " main  main main main";
}
.services .header {
  grid-area: header;
}
.services .mesa-de-trabajo {
  grid-area: main;
}
@media (min-width: 480px) {
  .services {
    grid-template-areas: " blanck header header header" " main main main main" " main main main main" " main  main main main";
  }
}

.mesa-de-trabajo {
  width: 100%;
  height: 90vh;
  padding: 50rem;
  background-image: url("../img/mesa-de-trabajo.webp");
  background-repeat: no-repeat;
  background-position: 50%;
  display: flex;
  align-items: center;
}
@media (min-width: 992px) and (min-width: 480px) {
  .mesa-de-trabajo {
    background-size: cover;
    align-items: inherit;
  }
}

@media (min-width: 480px) {
  .bets-version {
    width: 670rem;
  }
  .bets-version p {
    font-size: 32rem;
  }
}